.currentSlide{
    z-index:1;
  }
  .word{
    font-family: sans-serif;
    color:white;
    font-size: 60px;
  }
  
  .partOfSpeech:hover{
    transition: all 0.2s;
    color:white;
  }
  
  .partOfSpeech{
    font-size: 20px;
    display: inline;
    cursor: pointer;
    transition: all 0.2s;
    color:#9baec8;
  }
  
  .divider{
    font-size: 20px;
  }
  
  .definitions{
    transition: all 0.2s;
    font-style: italic;
    color:#9baec8;
    text-align: left;
  }
  
  .examples{
    transition: all 0.2s;
    font-style: italic;
    color:#9baec8;
    text-align: left;
  }
  
  .wordCardData{
    color:white;
    text-align: center;
  }
  
  .words{
    overflow-y: scroll;
    z-index: 1;
    background-color: #6d819c;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    height:500px;
    width:687.5px;
    scroll-behavior: smooth;
  }
  
  .words::-webkit-scrollbar{
    width:6px;
  }
  
  .words::-webkit-scrollbar-track{
    margin-top:20px;
    margin-bottom: 20px;
  }
  
  .words::-webkit-scrollbar-thumb{
    background-color:#8a9cb4;
    border-radius: 3px;
    
  }  
  
  
  @media screen and (max-width:768px){
    .words{
      width:310px;
      height:520px;
    }
    .word{
      word-break:break-all;
    }
  }